* {
    margin: 0;
    padding: 0;
    font-family: Georgia, 'Times New Roman', Times, serif;
    justify-content: center;
    align-items: center;
}

.site-name {
    display: flex;
    flex-flow: row;
    color: cornflowerblue;
    font-weight: bold ;
    justify-content: flex-start;
    margin-left: 30px;
    margin-top: 5vh;
}

.site-name h3 {
    --animate-duration: 5s;
}


.navbar-buttons {
    display: inline-flex;
    margin-left: 27px;
    justify-content: flex-start;
    align-items: center;

}

.button-styles {
    display: inline-flex;

}

.button-styles span {
    margin-left: 3px;
    margin-right: 2px;
    transition: 0.5ms ease-in;

}

.button-styles span:hover {
    background-color: blueviolet;
    transform: translateY(10px)
}

.nav-menu-bar {
    font-size: 2rem;
    margin-left: 43px;
}

.nav-menu-bar a {
    font-size: 2rem;
    margin-left: 43px;
}

section section span {
    /* background-color: cornflowerblue; */
    border-bottom: 5px solid ;
    border-right: 5px solid;
    border-radius: 4px;
    color: black;
}